import router from '../router'
router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.requireAuth)) { // 
    if (localStorage.getItem('user')) { // token
      next()
    } else {
      next({
        path: '/login',
        query: {redirect: to.fullPath} // path
      })
    }
  } else {
    next()
  }
})



